import Vue from 'vue'
import Router from 'vue-router'

/*引入page页面or组件资源*/
import Home from '@/pages/home'
import Foo from '@/pages/foo'
import Me from '@/pages/me'
import InputTags from '@/pages/inputTags'

//定义路由
Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [
        {
            path      : '',
            component : Home,
            meta      : {
                title        : 'ADE',
                judgeBrowser : true
            },
        },
        {
            path      : '/foo',
            component : Foo,
            meta      : {
                title        : '介绍页',
                judgeBrowser : true
            },
        },
        {
            path      : '/me',
            component : Me,
            meta      : {
                title        : '我的',
                judgeBrowser : true     
            },
        },
        {
            path      : '/inputTags',
            component : InputTags,
            meta      : {
                title        : 'input标签',
                judgeBrowser : true     
            },
        },
    ]
})